import addListenerPen from './instruments/pen';
import addListenerColorSelect from './instruments/colorSelect';
import addListenerBucket from './instruments/paintBucket';
import addListenerEreser from './instruments/ereser';
import addListenerStroke from './instruments/stroke';
import addListenerCircle from './instruments/circle';
import addListenerMove from './instruments/move';
import addListenerLighten from './instruments/lighten';
import addListenerDithering from './instruments/dithering';
import addListenerRectangle from './instruments/rectangle';
import addListenerDrawColor from './instruments/drawColor';
import addListenerToClear from './instruments/toClear';
import addListenerChangeBackground from './instruments/changeBackground';
import addListenerRandomPen from './instruments/randomPen';
import addListenerInversion from './instruments/inversion';
import addListenerMirroring from './instruments/mirroring';


export default class Instruments {
  constructor(instrument, size, color, buttonName) {
    this.targetInstrument = instrument;
    this.size = size;
    this.color = color;
    this.buttonName = buttonName;
  }

  start() {
    if (this.targetInstrument.classList[0] === 'instruments__buttons--pen') {
      addListenerPen(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--color-select') {
      addListenerColorSelect(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--paint-bucket') {
      addListenerBucket(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--ereser') {
      addListenerEreser(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--stroke') {
      addListenerStroke(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--circle') {
      addListenerCircle(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--move') {
      addListenerMove(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--lighten') {
      addListenerLighten(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--dithering') {
      addListenerDithering(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--rectangle') {
      addListenerRectangle(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--draw-color') {
      addListenerDrawColor(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--to-clear') {
      addListenerToClear(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--change-background') {
      addListenerChangeBackground(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--random-pen') {
      addListenerRandomPen(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--inversion') {
      addListenerInversion(this.size, this.color);
    } else if (this.targetInstrument.classList[0] === 'instruments__buttons--mirroring') {
      addListenerMirroring(this.size, this.color);
    }
  }
}
