import ModelStroke from '../../models/instruments/stroke';

function addListenerPen(size, color) {
  const targets = {
    targetStartX: 0,
    targetStartY: 0,
    targetEndX: 0,
    targetEndY: 0,
  };

  const canvas = document.querySelector('.canvas__general--working-canvas');

  canvas.onmousedown = (e) => {
    const arrColor = size.stack[size.activePage];
    const clone = JSON.parse(JSON.stringify(arrColor));

    targets.targetStartX = e.offsetX;
    targets.targetStartY = e.offsetY;

    let cloneFirst = new ModelStroke(size, color, targets, clone);

    canvas.onmousemove = (event) => {
      const cloneSecond = JSON.parse(JSON.stringify(arrColor));

      targets.targetEndX = event.offsetX;
      targets.targetEndY = event.offsetY;

      cloneFirst = new ModelStroke(size, color, targets, cloneSecond);
      cloneFirst.modelStrokeStart();
    };

    document.onmouseup = () => {
      size.stack[size.activePage] = cloneFirst.copyArr;
      canvas.onmousemove = null;
    };
  };
}

export default addListenerPen;
