import ModelPen from '../../models/instruments/pen';

function addListenerPen(size, color) {
  let targetX;
  let targetY;
  let arr = [];

  const canvas = document.querySelector('.canvas__general--working-canvas');

  canvas.onmousedown = () => {
    canvas.onmousemove = (event) => {
      targetX = event.offsetX;
      targetY = event.offsetY;
      arr.push(targetX, targetY);

      if (arr.length > 4) {
        arr.shift();
        arr.shift();
      }

      const modelPen = new ModelPen(size, color, targetX, targetY, arr);
      modelPen.modelPenStart();
    };
    document.onmouseup = () => {
      arr = [];
      canvas.onmousemove = null;
      document.onmouseup = null;
    };
  };
}

export default addListenerPen;
