import InstrumentView from '../../views/instrumentView';

export default class PaintBucket {
  constructor(size, color, targetX, targetY) {
    this.size = size;
    this.color = color;
    this.targetX = targetX;
    this.targetY = targetY;
    this.canvas = document.querySelector('.canvas__general--working-canvas');
  }

  start() {
    const startX = Math.floor(this.targetX / this.size.onePiksel);
    const startY = Math.floor(this.targetY / this.size.onePiksel);
    this.arr = this.size.stack[this.size.activePage];
    const arrSize = this.size.size;
    const startColor = this.arr[startY][startX];
    const stack = [[startY, startX]];
    if (startColor !== this.color.newColor) {
      while (stack.length) {
        const position = stack.pop();
        const x = position[0];
        const y = position[1];

        if ((x >= 0 && x < arrSize) && (y >= 0 && y < arrSize)) {
          const correntColor = this.arr[x][y];
          if (startColor === correntColor) {
            this.arr[x][y] = this.color.newColor;
            stack.push([x - 1, y], [x + 1, y], [x, y - 1], [x, y + 1]);
          }
        }
      }
    }
    const renderCanvas = new InstrumentView(this.arr, this.size);
    renderCanvas.viewStart();
  }
}
