import SizeView from '../../views/sizeView';

export default class AddPage {
  constructor(data) {
    this.data = data;
  }

  addPage() {
    this.getColor();
    this.viewNewPage();
  }

  getColor() {
    const defaultColorArr = [...Array(this.data.size)].map(() => Array(this.data.size));

    this.data.oldActivePage = this.data.activePage;

    this.data.activePage = this.data.stack.length || 1;

    for (let i = 0; i < this.data.size; i += 1) {
      for (let j = 0; j < this.data.size; j += 1) {
        defaultColorArr[i][j] = this.data.defaultColor;
      }
    }

    this.data.stack.push(defaultColorArr.map(arr => arr.slice()));
    this.data.buttonStyle = 'add';
  }

  viewNewPage() {
    const render = new SizeView();
    render.renderNewPage(this.data);
  }
}
