import ModelMove from '../../models/instruments/move';

function addListenerMove(data) {
  const targets = {
    targetStartX: 0,
    targetStartY: 0,
    targetEndX: 0,
    targetEndY: 0,
  };

  const canvas = document.querySelector('.canvas__general--working-canvas');

  canvas.onmousedown = (e) => {
    targets.targetStartX = e.offsetX;
    targets.targetStartY = e.offsetY;

    canvas.onmousemove = (event) => {
      targets.targetEndX = event.offsetX;
      targets.targetEndY = event.offsetY;

      const clone = [...Array(data.size)].map(() => Array(data.size));
      for (let i = 0; i < data.size; i += 1) {
        for (let j = 0; j < data.size; j += 1) {
          clone[i][j] = data.defaultColor;
        }
      }

      const absX = Math.abs(targets.targetEndX - targets.targetStartX);
      const absY = Math.abs(targets.targetEndY - targets.targetStartY);

      if (absX > data.onePiksel || absY > data.onePiksel) {
        const move = new ModelMove(data, targets, clone);
        move.modelMoveStart();
      }
    };

    canvas.onmouseup = () => {
      canvas.onmousemove = null;
      data.stack[data.activePage] = data.clone;
      canvas.onmouseup = null;
    };
  };
}

export default addListenerMove;
