import ModelEreser from '../../models/instruments/ereser';

function addListenerEreser(size, color) {
  let targetX;
  let targetY;
  let arr = [];

  const canvas = document.querySelector('.canvas__general--working-canvas');

  canvas.onmousedown = () => {
    canvas.onmousemove = (event) => {
      targetX = event.offsetX;
      targetY = event.offsetY;
      arr.push(targetX, targetY);

      if (arr.length > 4) {
        arr.shift();
        arr.shift();
      }

      const modelEreser = new ModelEreser(size, color, targetX, targetY, arr);
      modelEreser.modelEreserStart();

      document.onmouseup = () => {
        canvas.onmousemove = null;
        arr = [];
      };
    };
  };
}

export default addListenerEreser;
