import ColorSelect from '../../models/instruments/colorSelect';

function addListenerColor(size, color) {
  const canvas = document.querySelector('.canvas__general--working-canvas');

  canvas.onmousedown = (event) => {
    const targetX = event.offsetX;
    const targetY = event.offsetY;

    const colorSelect = new ColorSelect(size, color, targetX, targetY);
    colorSelect.start();
  };
}

export default addListenerColor;
