import ModelMirroring from '../../models/instruments/mirroring';

function addListenerMirroring(size, color) {
  let targetX;
  let targetY;
  let arr = [];

  const canvas = document.querySelector('.canvas__general--working-canvas');
  let targetColor = color.newColor;
  let contextMenu = false;

  canvas.oncontextmenu = () => {
    contextMenu = true;
  };

  canvas.onmousedown = () => {
    if (contextMenu) {
      targetColor = color.oldColor;
    }
    canvas.onmousemove = (event) => {
      targetX = event.offsetX;
      targetY = event.offsetY;
      arr.push(targetX, targetY);

      if (arr.length > 4) {
        arr.shift();
        arr.shift();
      }

      const modelMirroring = new ModelMirroring(size, targetColor, targetX, targetY, arr);
      modelMirroring.modelMirroringStart();

      document.onmouseup = () => {
        canvas.onmousemove = null;
        arr = [];
      };
    };
  };
}

export default addListenerMirroring;
