import KeyPress from '../controllers/keyPress';

function changeKey(checkSize, defaultColor) {
  const { button } = checkSize.tool;
  const { buttonName } = checkSize.tool;
  const { keys } = checkSize.tool;
  button.id = 'colorChangeKey';

  const arr = [];

  for (let i = 0; i < buttonName.length; i += 1) {
    arr.push(buttonName[i]);
  }

  const oldKey = arr[arr.length - 2];

  document.onkeypress = (event) => {
    const { key } = event;

    function findUsed() {
      for (let i = 0; i < keys.length; i += 1) {
        if (keys[i] === key) {
          return true;
        }
      }
      return false;
    }

    function findIndex() {
      for (let i = 0; i < keys.length; i += 1) {
        if (keys[i] === oldKey) {
          return i;
        }
      }
      return -1;
    }

    const keyUsed = findUsed();

    if (keyUsed !== true) {
      const index = findIndex();

      arr[arr.length - 2] = key;
      const str = arr.join('');
      button.innerText = str;

      checkSize.tool.keys[index] = key;
    }
    button.id = false;
    const keyPress = new KeyPress();
    keyPress.restoreEvents(checkSize, defaultColor);
  };
}

export default changeKey;
