import IsActivePage from './isActivePage';

export default class SizeView {
  constructor(data) {
    this.data = data;
    this.container = document.querySelector('.container');
    this.landing = document.querySelector('.landing');
    this.buttons = document.querySelector('.general__canvas-size');
    this.canvas = document.querySelector('.canvas__general--working-canvas');
    this.page = document.querySelector('.pages__page-operations--page');
    this.animation = document.querySelector('.container__animation--canvas');
    this.pages = document.querySelector('.pages__page-operations');
    this.delBut = document.querySelector('.pages__page-operations--del-page');
  }

  render() {
    this.delButtons();
    this.renderGeneralCanvas();
    this.renderPageCanvas();
  }

  view(ctx, fullSize, piksel) {
    for (let i = 0; i < fullSize; i += 1) {
      for (let j = 0; j < fullSize; j += 1) {
        ctx.fillStyle = this.data.stack[this.data.activePage][i][j];
        ctx.fillRect(piksel * j, piksel * i, piksel * (j + 1), piksel * (i + 1));
      }
    }
  }

  loadView(ctx, fullSize, piksel, page) {
    for (let i = 0; i < fullSize; i += 1) {
      for (let j = 0; j < fullSize; j += 1) {
        ctx.fillStyle = this.data.stack[page][i][j];
        ctx.fillRect(piksel * j, piksel * i, piksel * (j + 1), piksel * (i + 1));
      }
    }
  }

  delButtons() {
    this.landing.classList.add('none');
    this.container.classList.remove('none');
    this.canvas.classList.remove('none');
    this.buttons.classList.add('none');
    this.page.classList.remove('none');
    this.animation.classList.remove('none');
  }

  renderGeneralCanvas() {
    const generalCanvas = this.canvas;
    generalCanvas.id = `canvas${this.data.activePage}`;
    const ctx = generalCanvas.getContext('2d');
    const fullSize = this.data.size;
    const piksel = this.data.onePiksel;
    this.view(ctx, fullSize, piksel);
  }

  renderPageCanvas() {
    if (this.data.buttonStyle === 'add') {
      this.data.pages[this.data.activePage - 1].classList.remove('is-active-page');

      const newPage = document.createElement('canvas');
      newPage.classList.add('pages__page-operations--page');
      newPage.id = `page${this.data.activePage}`;

      newPage.width = 128;
      newPage.height = 128;
      this.pages.insertBefore(newPage, this.delBut);

      const ctx = newPage.getContext('2d');
      const fullSize = this.data.size;
      const piksel = this.data.onePiksel / 6;
      this.view(ctx, fullSize, piksel);

      const activePage = new IsActivePage(newPage);
      activePage.isActivePageView();

      this.data.pages.push(newPage);
    } else if (this.data.buttonStyle === 'clone') {
      const oldPage = document.getElementById(`page${this.data.activePage}`);
      this.data.pages[this.data.activePage - 1].classList.remove('is-active-page');

      const newPage = document.createElement('canvas');
      newPage.classList.add('pages__page-operations--page');

      newPage.width = 128;
      newPage.height = 128;
      this.pages.insertBefore(newPage, oldPage || this.delBut);

      const ctx = newPage.getContext('2d');
      const fullSize = this.data.size;
      const piksel = this.data.onePiksel / 6;
      this.view(ctx, fullSize, piksel);

      const activePage = new IsActivePage(newPage);
      activePage.isActivePageView();

      this.data.pages.splice(this.data.activePage, 0, newPage);

      this.refactorId();
    } else if (this.data.buttonStyle === 'del') {
      const canvas = this.data.pages[this.data.activePage];
      const ctx = canvas.getContext('2d');
      const fullSize = this.data.size;
      const piksel = this.data.onePiksel / 6;
      this.view(ctx, fullSize, piksel);
    } else if (this.data.buttonStyle === 'load') {
      const clearPage = document.querySelector('.pages__page-operations--page');
      this.pages.removeChild(clearPage);
      for (let i = 0; i < this.data.stack.length; i += 1) {
        const newPage = document.createElement('canvas');
        newPage.classList.add('pages__page-operations--page');
        newPage.id = `page${i}`;

        newPage.width = 128;
        newPage.height = 128;

        this.pages.insertBefore(newPage, this.delBut);

        const ctx = newPage.getContext('2d');
        const fullSize = this.data.size;
        const piksel = this.data.onePiksel / 6;
        this.loadView(ctx, fullSize, piksel, i);

        this.data.pages.shift();
        this.data.pages.push(newPage);
      }
      const page = document.querySelector(`#page${this.data.activePage}`);

      const activePage = new IsActivePage(page);
      activePage.isActivePageView();
    } else if (this.data.buttonStyle === 'move') {
      this.pages.removeChild(this.data.movePage);

      const newPage = document.createElement('canvas');
      newPage.classList.add('pages__page-operations--page');

      newPage.width = 128;
      newPage.height = 128;

      newPage.id = `page${this.data.activePage}`;

      this.pages.insertBefore(newPage, this.data.page2 || this.delBut);

      this.data.pages[this.data.activePage] = newPage;

      const ctx = newPage.getContext('2d');
      const fullSize = this.data.size;
      const piksel = this.data.onePiksel / 6;
      this.view(ctx, fullSize, piksel);

      const activePage = new IsActivePage(newPage);
      activePage.isActivePageView();

      this.refactorId();
    } else {
      const canvas = this.page;
      canvas.id = `page${this.data.activePage}`;
      const ctx = canvas.getContext('2d');
      const fullSize = this.data.size;
      const piksel = this.data.onePiksel / 6;
      this.view(ctx, fullSize, piksel);

      const activePage = new IsActivePage(canvas);
      activePage.isActivePageView();

      this.data.pages.push(canvas);
    }
  }

  renderNewPage(newData) {
    this.data = newData;
    const oldPage = document.getElementById(`page${this.data.oldActivePage}`);
    oldPage.classList.remove('is-active-page');
    this.render();
  }

  renderDelPage(newData) {
    this.data = newData;
    this.renderGeneralCanvas();
  }

  renderClonePage(newData) {
    this.data = newData;
    const oldPage = document.getElementById(`page${this.data.oldActivePage}`);
    oldPage.classList.remove('is-active-page');
    this.render();
  }

  renderMovePage(newData) {
    this.data = newData;
    this.render();
  }

  refactorId() {
    let id = 0;
    const number = this.data.pages.length;
    for (let i = 0; i < number; i += 1) {
      const page = this.data.pages[i];
      page.id = `page${id}`;
      id += 1;
    }
  }
}
