import InstrumentView from '../../views/instrumentView';

export default class ToClear {
  constructor(size) {
    this.size = size;
    this.canvas = document.querySelector('.canvas__general--working-canvas');
  }

  start() {
    this.arr = this.size.stack[this.size.activePage];
    const arrSize = this.size.size;
    const color = this.size.defaultColor;

    for (let i = 0; i < arrSize; i += 1) {
      for (let j = 0; j < arrSize; j += 1) {
        this.arr[j][i] = color;
      }
    }

    const renderCanvas = new InstrumentView(this.arr, this.size);
    renderCanvas.viewStart();
  }
}
