export default class ColorsView {
  constructor(newColorBlock, oldColorBlock, colors) {
    this.newColorBlock = newColorBlock;
    this.oldColorBlock = oldColorBlock;
    this.colors = colors;
  }

  render() {
    this.newColorBlock.style.backgroundColor = this.colors.newColor;
    this.oldColorBlock.style.backgroundColor = this.colors.oldColor;
  }
}
