import IsActiveView from '../views/isActiveView';
import IsNoActiveView from '../views/isNoActiveView';
import Instruments from './instrumentsTarget';

export default class KeyPress {
  constructor(checkSize, defaultColor) {
    this.checkSize = checkSize;
    this.defaultColor = defaultColor;
  }

  target() {
    this.addKeys();
    this.addListeners();
  }

  startInstrument() {
    const isActiveButton = this.checkSize.tool.button;
    const size = this.checkSize.data;
    const color = this.defaultColor.data;
    const buttonName = isActiveButton.innerText;
    const instruments = new Instruments(isActiveButton, size, color, buttonName);
    instruments.start();
  }

  addKeys() {
    this.checkSize.tool.keys = [];
    this.checkSize.tool.keys.push('p', 'c', 'b', 'e', 's', 'o', 'm', 'l', 'd', 'r', 'w', 't', 'k', 'x', 'i', 'g');
  }

  addListeners() {
    document.onkeypress = (event) => {
      this.checkSize.instruments = document.getElementsByClassName('instrument');

      for (let i = 0; i < this.checkSize.tool.keys.length; i += 1) {
        if (event.key === this.checkSize.tool.keys[i]) {
          if (this.checkSize.tool.isActive === true) {
            const view = new IsNoActiveView(this.checkSize);
            view.isNoActive();
            this.checkSize.tool.isActive = false;
          }

          this.checkSize.tool.button = this.checkSize.instruments[i];
          this.checkSize.tool.buttonName = this.checkSize.tool.button.innerText;

          const view = new IsActiveView(this.checkSize);

          view.isActive();
          this.checkSize.tool.isActive = true;
        }
      }
      this.startInstrument();
    };
  }

  restoreEvents(checkSize, defaultColor) {
    this.defaultColor = defaultColor;
    this.checkSize = checkSize;
    this.addListeners();
  }
}
