import IsActiveView from '../views/isActiveView';
import IsNoActiveView from '../views/isNoActiveView';
import Instruments from './instrumentsTarget';
import addListenerColor from './instruments/color';
import changeKey from '../models/changeKey';

export default class IsActive {
  constructor(checkSize, defaultColor) {
    this.checkSize = checkSize;
    this.defaultColor = defaultColor;
    this.checkSize.tool = {
      isActive: false,
    };
  }

  startInstrument() {
    const isActiveButton = this.checkSize.tool.button;
    const size = this.checkSize.data;
    const color = this.defaultColor.data;
    const instruments = new Instruments(isActiveButton, size, color);
    instruments.start();
  }

  target() {
    addListenerColor(this.defaultColor.data);

    const listInstruments = document.querySelector('.instruments__buttons');

    listInstruments.addEventListener('click', (event) => {
      if (event.target.closest('.instrument')) {
        if (this.checkSize.tool.isActive === true) {
          const view = new IsNoActiveView(this.checkSize);
          view.isNoActive();
          this.checkSize.tool.isActive = false;
        }

        const targetButton = event.target;
        this.checkSize.tool.button = targetButton;

        const view = new IsActiveView(this.checkSize);
        view.isActive();

        this.checkSize.tool.isActive = true;
        this.checkSize.tool.buttonName = this.checkSize.tool.button.innerText;
      }
      this.startInstrument();

      if (event.target.classList[0] === 'instruments__buttons--change-key') {
        if (this.checkSize.tool.isActive === true) {
          changeKey(this.checkSize, this.defaultColor);
        }
      }
    });
  }
}
