import DefaultColor from '../colors';

function addListenerColor(color) {
  const input = document.querySelector('.instrument__colors--input');
  const colorButton = document.querySelector('.instruments__colors--new-color');
  const oldColors = color;

  function colorSwap(newColor, colors) {
    const newColors = colors;

    newColors.oldColor = newColors.newColor;
    newColors.newColor = newColor;

    const defaultColor = new DefaultColor(newColors.newColor, newColors.oldColor);
    defaultColor.start();
  }

  colorButton.onmousedown = () => {
    input.click();

    input.onchange = () => colorSwap(input.value, oldColors);
  };
}

export default addListenerColor;
