import SizeView from '../../views/sizeView';

export default class ClonePage {
  constructor(data) {
    this.data = data;
  }

  clonePage() {
    this.getColor();
    this.viewNewPage();
    // this.refactorId();
  }

  getColor() {
    const defaultColorArr = [...Array(this.data.size)].map(() => Array(this.data.size));

    this.data.oldActivePage = this.data.activePage;

    this.data.oldPage = this.data.stack[this.data.oldActivePage];

    this.data.activePage += 1;

    for (let i = 0; i < this.data.size; i += 1) {
      for (let j = 0; j < this.data.size; j += 1) {
        defaultColorArr[i][j] = this.data.oldPage[i][j];
      }
    }

    this.data.stack.splice(this.data.oldActivePage, 0, defaultColorArr.map(arr => arr.slice()));
    this.data.buttonStyle = 'clone';
  }

  viewNewPage() {
    const render = new SizeView();
    render.renderClonePage(this.data);
  }
}
