import InstrumentView from '../../views/instrumentView';

export default class TargetPage {
  constructor(data, event) {
    this.data = data;
    this.event = event;
    this.canvas = document.querySelector('.pages__page-operations--page');
  }

  target() {
    this.data.oldActivePage = this.data.activePage;

    this.idStr = this.event.target.id;
    this.id = +this.idStr.replace(/\D+/g, '');

    if (this.id !== this.data.activePage) {
      this.data.activePage = this.id;

      const targetPageView = new InstrumentView();
      targetPageView.viewPageStart(this.data);
    }
  }
}
