import DefaultColor from '../../controllers/colors';

export default class ColorSelect {
  constructor(size, color, targetX, targetY) {
    this.size = size;
    this.color = color;
    this.targetX = targetX;
    this.targetY = targetY;
    this.canvas = document.querySelector('.canvas__general--working-canvas');
  }

  start() {
    this.canvasX = Math.floor(this.targetX / this.size.onePiksel);
    this.canvasY = Math.floor(this.targetY / this.size.onePiksel);

    this.color.oldColor = this.color.newColor;
    this.color.newColor = this.size.stack[this.size.activePage][this.canvasY][this.canvasX];

    const defaultColor = new DefaultColor(this.color.newColor, this.color.oldcolor);
    defaultColor.start();
  }

  static ololo(a, b) {
    return a + b;
  }
}
