import DrawColor from '../../models/instruments/drawColor';

function addListenerDrawColor(size, color) {
  const canvas = document.querySelector('.canvas__general--working-canvas');
  let targetColor = color.newColor;
  let contextMenu = false;

  canvas.oncontextmenu = () => {
    contextMenu = true;
  };

  canvas.onmousedown = (event) => {
    if (contextMenu) {
      targetColor = color.oldColor;
    }
    const targetX = event.offsetX;
    const targetY = event.offsetY;

    const colorSelect = new DrawColor(size, targetColor, targetX, targetY);
    colorSelect.start();
  };
}

export default addListenerDrawColor;
