import ModelLighten from '../../models/instruments/lighten';

function addListenerLighten(data, color) {
  let targetX;
  let targetY;

  const canvas = document.querySelector('.canvas__general--working-canvas');

  canvas.onmousedown = () => {
    canvas.onmousemove = (event) => {
      targetX = event.offsetX;
      targetY = event.offsetY;

      const modelLighten = new ModelLighten(data, color, targetX, targetY);
      modelLighten.modelLightenStart();

      document.onmouseup = () => {
        canvas.onmousemove = null;
      };
    };
  };
}

export default addListenerLighten;
