import InstrumentView from '../../views/instrumentView';

export default class ModelRictangle {
  constructor(size, color, targets, copyArr) {
    this.size = size;
    this.color = color;
    this.targets = targets;
    this.copyArr = copyArr;
  }

  modelRictangleStart() {
    this.target();
    this.canvasArr();
  }

  target() {
    this.oldCanvasX = Math.floor(this.targets.targetStartX / this.size.onePiksel);
    if (this.oldCanvasX >= this.size.size) { this.oldCanvasX = this.size.size - 1; }

    this.oldCanvasY = Math.floor(this.targets.targetStartY / this.size.onePiksel);
    if (this.oldCanvasY >= this.size.size) { this.oldCanvasY = this.size.size - 1; }

    this.newCanvasX = Math.floor(this.targets.targetEndX / this.size.onePiksel);
    if (this.newCanvasX >= this.size.size) { this.newCanvasX = this.size.size - 1; }

    this.newCanvasY = Math.floor(this.targets.targetEndY / this.size.onePiksel);
    if (this.newCanvasY >= this.size.size) { this.newCanvasY = this.size.size - 1; }
  }

  canvasArr() {
    this.newColor = this.color.newColor;

    if (this.oldCanvasX < this.newCanvasX) {
      for (let x = this.oldCanvasX; x < this.newCanvasX; x += 1) {
        this.copyArr[this.oldCanvasY][x] = this.newColor;
        this.copyArr[this.newCanvasY][x] = this.newColor;
      }
    } else {
      for (let x = this.oldCanvasX; x > this.newCanvasX; x -= 1) {
        this.copyArr[this.oldCanvasY][x] = this.newColor;
        this.copyArr[this.newCanvasY][x] = this.newColor;
      }
    }

    if (this.oldCanvasY < this.newCanvasY) {
      for (let y = this.oldCanvasY; y < this.newCanvasY; y += 1) {
        this.copyArr[y][this.oldCanvasX] = this.newColor;
        this.copyArr[y][this.newCanvasX] = this.newColor;
      }
    } else {
      for (let y = this.oldCanvasY; y > this.newCanvasY; y -= 1) {
        this.copyArr[y][this.oldCanvasX] = this.newColor;
        this.copyArr[y][this.newCanvasX] = this.newColor;
      }
    }
    this.copyArr[this.newCanvasY][this.newCanvasX] = this.newColor;

    const renderCanvas = new InstrumentView(this.copyArr, this.size);
    renderCanvas.viewStart();
  }
}
