import SizeModel from '../models/sizeModel';
import SizeView from '../views/sizeView';
import Animation from '../models/animation';
import Coordinates from './coordinates';

export default class Size {
  constructor() {
    this.buttonSize32 = document.querySelector('.general__canvas-size--32x32');
    this.buttonSize64 = document.querySelector('.general__canvas-size--64x64');
    this.buttonSize128 = document.querySelector('.general__canvas-size--128x128');
    this.buttonLoad = document.querySelector('.general__canvas-size--load');
  }

  start() {
    this.addListeners();
  }

  next(size) {
    this.model = new SizeModel(size);
    this.data = this.loadData || this.model.getModel();
    this.view = new SizeView(this.data);
    this.view.render();
    this.animation = new Animation(this.data);
    this.animation.start();
    this.coordinates = new Coordinates(this.data);
    this.coordinates.display();
  }

  addListeners() {
    this.buttonSize32.addEventListener('click', () => {
      const size = 32;
      this.next(size);
    });
    this.buttonSize64.addEventListener('click', () => {
      const size = 64;
      this.next(size);
    });
    this.buttonSize128.addEventListener('click', () => {
      const size = 128;
      this.next(size);
    });
    this.buttonLoad.addEventListener('click', () => {
      this.loadData = JSON.parse(localStorage.getItem('data'));
      const { size } = this.loadData;
      this.loadData.buttonStyle = 'load';
      this.next(size);
    });
  }
}
