import ChangeBackground from '../../models/instruments/changeBackground';

function addListenerChangeBackground(data, color) {
  const canvas = document.querySelector('.canvas__general--working-canvas');
  let targetColor = color.newColor;
  let contextMenu = false;

  canvas.oncontextmenu = () => {
    contextMenu = true;
  };

  canvas.onmousedown = () => {
    if (contextMenu) {
      targetColor = color.oldColor;
    }
    const changeBackground = new ChangeBackground(data, targetColor);
    changeBackground.start();
  };
}

export default addListenerChangeBackground;
