import saveLocalstorage from '../models/saveLocalstorage';

export default class Save {
  constructor(data, color) {
    this.data = data;
    this.color = color;
    this.saveButton = document.querySelector('.container__animation--save');
  }

  saveData() {
    this.saveButton.onclick = () => {
      saveLocalstorage(this.data, this.color);
    };
  }
}
