import AddPage from '../models/Pages/addPage';
import DelPage from '../models/Pages/delPage';
import ClonePage from '../models/Pages/clonePage';
import TargetPage from '../models/Pages/targetPage';
import MovePage from '../models/Pages/movePage';

export default class Pages {
  constructor(data) {
    this.data = data;
    this.canvas = document.querySelector('.pages__page-operations--page');
    this.pages = document.querySelector('.pages__page-operations');
  }

  isActive() {
    this.addListeners();
  }

  addListeners() {
    const addPage = document.querySelector('.pages__page-operations--add-page');
    const delPage = document.querySelector('.pages__page-operations--del-page');
    const clonePage = document.querySelector('.pages__page-operations--clone-page');

    addPage.onclick = () => this.addPage();
    delPage.onclick = () => this.delPage();
    clonePage.onclick = () => this.clonePage();

    this.pages.onclick = (event) => {
      if (event.target.localName === 'canvas') {
        const targetPage = new TargetPage(this.data.data, event);
        targetPage.target();
      }
    };

    this.pages.onmousedown = (eventDown) => {
      let targetStartY = eventDown.screenY;
      // const page = eventDown.target;
      // const pageId = page.id[page.id.length - 1];

      if (eventDown.target.localName === 'canvas') {
        this.pages.onmousemove = (eventMove) => {
          const targetEndY = eventMove.screenY;
          const difrenceY = Math.abs(targetStartY - targetEndY);
          if (difrenceY > 100) {
            const movePage = new MovePage(this.data, targetStartY, targetEndY, eventMove);
            movePage.startMove();
          }
          this.pages.onmouseup = () => {
            const movePageUp = new MovePage(this.data);
            movePageUp.startMoveAfterUp();
            targetStartY = 0;
            this.pages.onmousemove = null;
            this.pages.onmouseup = null;
          };
        };
        this.pages.onmouseup = () => {
          this.pages.onmousemove = null;
          this.pages.onmouseup = null;
        };
      }
    };
  }

  addPage() {
    const newPage = new AddPage(this.data.data);
    newPage.addPage();
  }

  delPage() {
    const delPage = new DelPage(this.data.data);
    delPage.delPage();
  }

  clonePage() {
    const clonePage = new ClonePage(this.data.data);
    clonePage.clonePage();
  }
}
