import ModelDithering from '../../models/instruments/dithering';

function addListenerDithering(size, color) {
  let targetX;
  let targetY;
  let arr = [];
  const canvas = document.querySelector('.canvas__general--working-canvas');

  canvas.onmousedown = () => {
    canvas.onmousemove = (event) => {
      targetX = event.offsetX;
      targetY = event.offsetY;
      arr.push(targetX, targetY);

      if (arr.length > 4) {
        arr.shift();
        arr.shift();
      }

      const modelDithering = new ModelDithering(size, color, targetX, targetY, arr);
      modelDithering.modelDitheringStart();

      document.onmouseup = () => {
        canvas.onmousemove = null;
        arr = [];
      };
    };
  };
}

export default addListenerDithering;
