import InstrumentView from '../../views/instrumentView';

export default class ChangeBackground {
  constructor(data, color) {
    this.data = data;
    this.color = color;
    this.canvas = document.querySelector('.canvas__general--working-canvas');
  }

  start() {
    const arr = this.data.stack[this.data.activePage];
    const arrSize = this.data.size;
    this.oldBackgroundColor = this.data.defaultColor;
    const newColor = this.color;
    this.data.defaultColor = this.color;

    for (let i = 0; i < arrSize; i += 1) {
      for (let j = 0; j < arrSize; j += 1) {
        if (arr[j][i] === this.oldBackgroundColor) arr[j][i] = newColor;
      }
    }
    const renderCanvas = new InstrumentView(arr, this.data);
    renderCanvas.viewStart();
  }
}
