import Render from '../views/renderAnimation';

export default class Animation {
  constructor(data) {
    this.data = data;
    this.range = document.querySelector('.container__animation--fps');
    this.frame = 0;
  }

  start() {
    this.newFps = this.range.value;
    const newRange = this.range;

    newRange.addEventListener('change', () => {
      this.newFps = +newRange.value;
      clearInterval(this.set);

      if (this.newFps !== 0) {
        this.interval(this.newFps);
      }
    });

    this.interval(this.newFps);
  }

  interval() {
    this.set = setInterval(() => {
      if (this.frame > this.data.stack.length - 1) {
        this.frame = 0;
      }

      const render = new Render(this.data, this.frame);
      render.viewAnimation();

      this.frame += 1;
    }, 1000 / this.newFps);
  }
}
