export default class SizeModel {
  constructor(size) {
    this.size = size;
    this.windowSize = 768;
    this.data = {
      defaultColor: '#000000',
      stack: [],
      pages: [],
    };
  }

  getModel() {
    this.getColor();
    this.getSize();
    return this.data;
  }

  getColor() {
    const defaultColorArr = [...Array(this.size)].map(() => Array(this.size));

    for (let i = 0; i < this.size; i += 1) {
      for (let j = 0; j < this.size; j += 1) {
        defaultColorArr[i][j] = this.data.defaultColor;
      }
    }

    this.data.stack.push(defaultColorArr.map(arr => arr.slice()));
  }

  getSize() {
    this.data.activePage = 0;

    this.data.onePiksel = this.windowSize / this.size;
    this.data.size = this.size;
  }
}
