import InstrumentView from '../../views/instrumentView';

export default class ModelMove {
  constructor(data, targets, clone) {
    this.data = data;
    this.targets = targets;
    this.clone = clone;
  }

  modelMoveStart() {
    this.target();
    this.behavior();
  }

  target() {
    this.oldCanvasX = Math.floor(this.targets.targetStartX / this.data.onePiksel);
    if (this.oldCanvasX >= this.data.size) { this.oldCanvasX = this.data.size - 1; }

    this.oldCanvasY = Math.floor(this.targets.targetStartY / this.data.onePiksel);
    if (this.oldCanvasY >= this.data.size) { this.oldCanvasY = this.data.size - 1; }

    this.newCanvasX = Math.floor(this.targets.targetEndX / this.data.onePiksel);
    if (this.newCanvasX >= this.data.size) { this.newCanvasX = this.data.size - 1; }

    this.newCanvasY = Math.floor(this.targets.targetEndY / this.data.onePiksel);
    if (this.newCanvasY >= this.data.size) { this.newCanvasY = this.data.size - 1; }
  }

  behavior() {
    const arr = this.data.stack[this.data.activePage];
    const differenceX = this.newCanvasX - this.oldCanvasX;
    const differenceY = this.newCanvasY - this.oldCanvasY;
    this.data.clone = this.clone;

    for (let i = this.data.size - 1; i >= 0; i -= 1) {
      for (let j = this.data.size - 1; j >= 0; j -= 1) {
        if (arr[j][i] !== this.data.defaultColor) {
          this.clone[j + differenceY][i + differenceX] = arr[j][i];
        }
      }
    }

    const renderCanvas = new InstrumentView(this.clone, this.data);
    renderCanvas.viewStart();
  }
}
