import DelPageView from '../../views/delPageView';
// import refactorId from './refactorId';

export default class DelPage {
  constructor(data) {
    this.data = data;
    this.pages = document.querySelector('.pages__page-operations');
  }

  delPage() {
    this.delData();
    this.refactorId();
  }

  delData() {
    if (this.data.pages.length > 1) {
      this.data.oldActivePage = this.data.activePage;

      this.pages.removeChild(this.data.pages[this.data.activePage]);

      this.data.buttonStyle = 'del';

      this.data.pages.splice(this.data.activePage, 1);
      this.data.stack.splice(this.data.activePage, 1);

      // refactorId(this.data);

      const viewNewPage = new DelPageView(this.data);
      viewNewPage.delPage();
    }
  }

  refactorId() {
    let id = 0;
    const number = this.data.stack.length + 1;
    for (let i = 0; i < number; i += 1) {
      const page = document.getElementById(`page${i}`);
      if (page !== null) {
        page.id = `page${id}`;
        id += 1;
      }
    }
  }
}
