import ColorsModel from '../models/defaultColorModel';
import ColorsView from '../views/defaultColorView';

export default class DefaultColor {
  constructor(newColor, oldColor) {
    this.newColor = newColor || '#ffffff';
    this.oldColor = oldColor || '#000000';
    this.newColorBlock = document.querySelector('.instruments__colors--new-color');
    this.oldColorBlock = document.querySelector('.instruments__colors--old-color');
    this.inputColor = document.querySelector('.instrument__colors--input');
  }

  start() {
    this.model = new ColorsModel(this.newColor, this.oldColor);
    this.data = this.model.getModel();

    this.view = new ColorsView(this.newColorBlock, this.oldColorBlock, this.data);
    this.view.render();
  }
}
