import SizeView from './sizeView';

export default class DelPageView {
  constructor(data) {
    this.data = data;
    this.page = document.querySelector('.pages__page-operations');
  }

  delPage() {
    this.data.activePage -= 1;

    if (this.data.activePage < 0) { this.data.activePage = 0; }

    this.data.pages[this.data.activePage].classList.add('is-active-page');

    const render = new SizeView();
    render.renderDelPage(this.data);
  }
}
