import InstrumentView from '../../views/instrumentView';

export default class ModelLighten {
  constructor(data, color, targetX, targetY) {
    this.canvas = document.querySelector('.canvas__general--working-canvas');
    this.data = data;
    this.color = color;
    this.targetX = targetX;
    this.targetY = targetY;
  }

  modelLightenStart() {
    this.target();
    this.canvasArr();
  }

  target() {
    this.canvasX = Math.floor(this.targetX / this.data.onePiksel);
    if (this.canvasX >= this.data.size) { this.canvasX = this.data.size - 1; }

    this.canvasY = Math.floor(this.targetY / this.data.onePiksel);
    if (this.canvasY >= this.data.size) { this.canvasY = this.data.size - 1; }
  }

  canvasArr() {
    this.stack = this.data.stack[this.data.activePage];
    this.data.oldColor = this.stack[this.canvasY][this.canvasX];

    if (this.data.oldColor !== '#ffffff') {
      this.r = this.data.oldColor.substr(1, 2);
      this.g = this.data.oldColor.substr(3, 2);
      this.b = this.data.oldColor.substr(5, 2);

      const tenR = parseInt(this.r, 16);
      const tenG = parseInt(this.g, 16);
      const tenB = parseInt(this.b, 16);

      let newR = tenR + 25;
      if (newR > 255) { newR = 255; }
      let newG = tenG + 25;
      if (newG > 255) { newG = 255; }
      let newB = tenB + 25;
      if (newB > 255) { newB = 255; }

      const newColorR = newR.toString(16);
      const newColorG = newG.toString(16);
      const newColorB = newB.toString(16);

      const newColor = `#${newColorR}${newColorG}${newColorB}`;

      this.stack[this.canvasY][this.canvasX] = newColor;
    }

    const renderCanvas = new InstrumentView(this.stack, this.data);
    renderCanvas.viewStart();
  }
}
