import DefaultColor from './controllers/colors';
import Size from './controllers/sizes';
import IsActive from './controllers/isActive';
import KeyPress from './controllers/keyPress';
import Pages from './controllers/pages';
import FullScreen from './controllers/fullScreen';
import Save from './controllers/save';

import './style.css';

const defaultColor = new DefaultColor();
defaultColor.start();

const checkSize = new Size();
checkSize.start();

const isActive = new IsActive(checkSize, defaultColor);
isActive.target();

const keyPress = new KeyPress(checkSize, defaultColor);
keyPress.target();

const pages = new Pages(checkSize);
pages.isActive();

const fullScreen = new FullScreen();
fullScreen.startFullScreen();

const save = new Save(checkSize, defaultColor);
save.saveData();
