export default class Coordinates {
  constructor(size, defaultColor) {
    this.size = size;
    this.default = defaultColor;
    this.body = document.querySelector('body');
    this.canvas = document.querySelector('.canvas__general--working-canvas');
    this.coords = document.querySelector('.coords');
  }

  display() {
    this.body.addEventListener('mousemove', (e) => {
      if (e.target === this.canvas) {
        const coordsX = Math.floor(e.offsetX / this.size.onePiksel) + 1;
        if (this.coordsX >= this.size.size) { this.coords = this.size.size - 1; }

        const coordsY = Math.floor(e.offsetY / this.size.onePiksel) + 1;
        if (this.coordsY > this.size.size) { this.coordsY = this.size.size - 1; }

        this.coords.innerHTML = `${coordsX}x${coordsY}`;
      } else {
        this.coords.innerHTML = '';
      }
    });
  }
}
