import PaintBucket from '../../models/instruments/paintBucket';

function eventListenerBucket(size, color) {
  const canvas = document.querySelector('.canvas__general--working-canvas');

  canvas.onmousedown = (event) => {
    const targetX = event.offsetX;
    const targetY = event.offsetY;
    const paintBucket = new PaintBucket(size, color, targetX, targetY);
    paintBucket.start();
  };
}

export default eventListenerBucket;
