import SizeView from '../../views/sizeView';

export default class MovePage {
  constructor(data, startY, endY, eventMove) {
    this.data = data.data;
    this.startY = startY;
    this.endY = endY;
    this.eventMove = eventMove;
  }

  startMove() {
    this.data.movePage = this.data.pages[this.data.activePage];
    this.data.movePage.style.transform = 'scale(1.2, 1.2)';
    const oldPositionMoveY = +this.data.movePage.id[this.data.movePage.id.length - 1];
    const positionMoveY = this.endY - this.startY;

    const description = Math.floor(positionMoveY / 138);
    if (description) {
      this.data.newPage = description + oldPositionMoveY;
      if (Math.abs(this.data.oldPage - this.data.newPage) > 0) {
        this.data.page1.classList.remove('after1');
        this.data.page2.classList.remove('after2');
      }
      this.data.oldPage = this.data.newPage;
      this.data.delBut = document.querySelector('.pages__page-operations--del-page');
      this.data.addBut = document.querySelector('.pages__page-operations--add-page');
      this.data.page1 = document.getElementById(`page${this.data.newPage}`) || this.data.addBut;
      this.data.page2 = document.getElementById(`page${this.data.newPage + 1}`) || this.data.delBut;
      this.data.page1.classList.add('after1');
      this.data.page2.classList.add('after2');
    }
  }

  startMoveAfterUp() {
    this.data.movePage.style.transform = '';
    this.data.buttonStyle = 'move';

    this.data.moveStack = this.data.stack[this.data.activePage];
    this.data.stack.splice(this.data.activePage, 1);
    const stack1 = this.data.page1.id[this.data.page1.id.length - 1];
    const stack2 = this.data.page2.id[this.data.page2.id.length - 1];
    if (this.data.activePage < this.data.newPage) {
      this.data.stack.splice(stack1, 0, this.data.moveStack);
    } else {
      this.data.stack.splice(stack2, 0, this.data.moveStack);
    }

    this.data.movePage = this.data.pages[this.data.activePage];
    this.data.pages.splice(this.data.activePage, 1);
    const page1 = this.data.page1.id[this.data.page1.id.length - 1];
    const page2 = this.data.page2.id[this.data.page2.id.length - 1];
    if (this.data.activePage < this.data.newPage) {
      this.data.pages.splice(page1, 0, this.data.movePage);
      this.data.activePage = page1;
    } else {
      this.data.pages.splice(page2, 0, this.data.movePage);
      this.data.activePage = page2;
    }

    this.data.page1.classList.remove('after1');
    this.data.page2.classList.remove('after2');

    const render = new SizeView();
    render.renderMovePage(this.data);
  }
}
